*{
    margin: 0px;
    padding: 0px;
    box-sizing: border-box;
}

body{
    background-image: url("theme/bg/bg05.png");
}

#header{
    background-color: white;
    z-index: 100;
    width: 100vw;
    height: max-content;
    position: fixed;
    top: 0;
    left:0;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 5px 20px 5px 10px;
    box-sizing: border-box;
    border-bottom: 1px solid black;
    align-items: center;
}

#header>#imgContainer{
    width:10vw;
}
#logo{
    width: 10vw;
}
#header>#menuTogller{
    display: none;
}

#navigation-menu{
    display: flex;
    width: 40%;
    justify-content: flex-end;
    gap: 30px;
    font-family:'Courier New', Courier, monospace;
    font-size: 30px;
}

#navigation-menu>.link{
    color: black;
    text-decoration: none;
}

#navigation-menu>.link:hover{
    color: blue !important;
}

.container{
    /* margin-top:25vh; */
    position: relative;
    padding: 0px 20px;
}


.container>div>h2{
    font-size: 2rem;
    display: flex;
    flex-wrap: wrap;
    font-family:'Courier New', Courier, monospace;
}

.typo{
    /* position: relative; */
    display: flex;
    position: relative;
    font-size: 2rem;
    color: red;
    overflow: hidden;
    animation: typograph 2s steps(13) ;
    /* animation-delay: 0.5s; */
}

@keyframes typograph{
    0%{
        width: 0ch;
    }
    100%{
        width: 13ch;
    }
}

.content{
    font-size: 1rem;
    font-family: monospace;
    margin: 3rem;
    padding: 15px;
    border-left: 1px solid grey;
}

.out{
    transition: 0.5s;
    transform: translate(-100%);
}

.in{
    transform: translateX(0);
    transition: 0.5s;
}
#cardContainer{
    display: flex;
    flex-wrap: wrap;
    /* justify-content: space-between; */
    /* padding: 1.5rem; */
    padding: 13vh 1.5rem
}
.card{
    display: flex;
    padding: 1rem;
    flex-direction: column;
    border: 1px solid black;
    border-radius: 1rem;
    margin-bottom: 1rem;
    margin-left: 4%;
    height: calc(max(200px, 30%));
    width: calc(max(140px, 27vw));
    justify-content: space-between;
    align-items: center;
    background-color: rgba(147, 136, 136, 0.2);
}
.details{
    align-items: center;
    text-align: center;
    padding-top: 5px;
    font-size: 0.7rem;
    display: flex;
    flex-direction: column;
    gap:10px;
}
.card>img{
    width:100%;
    padding-bottom: 1rem;
    margin-bottom: 1rem;
    border-bottom: 1px solid black;

}

@media screen and (max-width: 550px) {
    #header>#menuTogller{
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        width: 13vw;
        /* background-color: aqua; */
    }
    #header>#menuTogller>.stick{
        width: 100%;
        height: 7px;
        background-color: grey;
        margin: 2px;
        border-radius: 7px;
        transition-duration: 1s;
    }

    .content{
        margin: 0;
        font-size: 1rem;
    }
    #logo{
        /* transform: scale(0.7); */
        width: 30vw;
    }
    #navigation-menu{
        display: none;
        width: 100%;
        /* display: flex; */
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 0px;
        /* height: 90vh; */
        transition-duration: 1s;
        overflow: hidden;
    }
}